import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Card from '@material-ui/core/Card';

export default class card extends Component {
	render() {
		const {hit}=this.props;
		return (<Link to={`${hit.name}`}>
       
	<Card>
		<div className='pt1 white' style={{position:'relative',height:'auto', width:'130px',  backgroundColor:'#ff944d'}}>
		<img  className=''  height='60px'  alt={hit.name} src={require(`../../img/${hit.name}.png`)}
          />
    <div className='truncate w-90 f6  tc pt1 hidden dib'>
		{hit.name} 
         </div>	
         <div className='truncate w-90 f7 tc  hidden dib'>
		{hit.hindi} 
         </div>	 </div>
			  </Card>  </Link>
              		);
	}
}
