import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Card from './Card';
import Headers from './Header'
import Zoom from './Zoom'

export default class card extends Component {
    constructor(props){
        super(props)
        this.state={
            data:{},
            currentData:{},
            index:0,
            size:0,
            key:''
        }
    }
    getData=()=>{
        var data={}
        this.props.db.collection('panchang').doc('1').get()
        .then(function(doc) {
            if (doc.exists) {
              data=doc.data()
               console.log(doc.data())
            } else {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
        }).then(d=>{
            this.setState({data:data})
            this.setState({index:Object.keys(this.state.data).length-1})
            this.setState({size:Object.keys(this.state.data).length})
            this.setState({currentData:data[Object.keys(data)[this.state.index]]})
           this.setState({key:Object.keys(this.state.data)[this.state.index]}) 
            
        })
    }
    updateData=(ind)=>{
        this.setState({currentData:this.state.data[Object.keys(this.state.data)[ind]]})
    }
    componentDidMount(){
        if(this.props.db){
            this.getData()
        }
    }
    updateIndex=(dir)=>{
        if(dir==='+'){
            var ind = this.state.index
            this.setState({index:this.state.index+1})
            this.updateData(ind+1)
        }else{
            var ind = this.state.index
            this.setState({index:this.state.index-1})
            this.updateData(ind-1)
        }

    }
	render() {
		console.log(this.state.data)
		return (
        <div>
            <Headers data={this.state.key} updateIndex={this.updateIndex} size={this.state.size} index={this.state.index}/>
            <div style={{height:'100%',width:'100%'}}>
            <Zoom checked={true}>
            <div className='grid'>
      <div className='flex tc flex-wrap'>
    {this.state.currentData!=={}?Object.keys(this.state.currentData).map((item)=>{
        return <Card name={item} data={this.state.currentData[item]}/>
             }) :null}
      </div>
			</div>
	
               </Zoom>
            </div>
        </div>      		);
	}
}
