import ImageGallery from 'react-image-gallery';
import React from 'react'
import "react-image-gallery/styles/scss/image-gallery.scss";
import "react-image-gallery/styles/css/image-gallery.css";
import "react-image-gallery/styles/css/image-gallery.css";
import img3 from "../../../img/img3.jpeg"
import img1 from "../../../img/img1.jpeg"
import img2 from "../../../img/img2.jpeg"
import img4 from "../../../img/img4.jpeg"
import img5 from "../../../img/img5.jpeg"
import img6 from "../../../img/img6.jpeg"

//# Stylesheet with no icons
//node_modules/react-image-gallery/styles/scss/image-gallery-no-icon.scss
//node_modules/react-image-gallery/styles/css/image-gallery-no-icon.css 
export default class MyComponent extends React.Component {

  render() {

    const images = [
      {
        original:img1 ,
        thumbnail: img1,
      },
      {
        original: img2,
        thumbnail: img2
      },
      {
        original: img3,
        thumbnail: img3
      },{
        original:img4 ,
        thumbnail: img4,
      },
      {
        original: img5,
        thumbnail: img5
      },
      {
        original: img6,
        thumbnail: img6
      }
    ]

    return (
        <div className='mt3 ' >
      <ImageGallery showPlayButton={false} showBullets={true} autoPlay={true} showFullscreenButton={false} showThumbnails={false} items={images} />
      </div> );
  }

}