import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import DatePicker from 'react-datepicker'
import "react-datepicker/dist/react-datepicker.css";
export default class card extends Component {
    state={
        name:'',
        time:'',
        date:new Date('2014-08-18T21:11:54'),
        birthPlace:'',
        question:''
    }
    onSubmit=()=>{
        var text="name:"+this.state.name+"\n time:"+this.state.time+"\n date:"+this.state.date+"\n Birth Place:"+this.state.birthPlace+"\n Question:"+this.state.question        
      window.open(`https://wa.me/919795751332/?text=${text}`, "_blank") 
    }
    handleChange=name=>event=>{
      if(name==='date'||name==='time'){
        this.setState({[name]:event})
      }else{
      this.setState({[name]:event.target.value})}
    }
    handleDate=(date)=>{
      console.log(date)
      this.setState({date:date})
    }
	render() {
		
		return (
        <div className='bg-white  h-100 pa3 ma2' style={{marginTop: "6.5rem"}}>
         <span>To ask the question to Pandit Ravi Shankar Pandey</span>

         <TextField
        id="standard-textarea"
        label="Name"
        multiline
        fullWidth
        onChange={this.handleChange("name")}
        margin="normal"
      />
      <TextField
        id="standard-textarea"
        label="Birth Place"
        multiline
        fullWidth
        onChange={this.handleChange("birthPlace")}
        margin="normal"
      />
      <div className='flex mt2 '>
      <span className='mr2'>Birth date and time: </span>
     <DatePicker
    selected={this.state.date}
    onChange={this.handleDate}
    showTimeSelect
    timeFormat="HH:mm"
    timeIntervals={15}
    dateFormat="MMMM d, yyyy h:mm aa"
    timeCaption="time"
/></div>
      <TextField
        id="standard-textarea"
        label="Question"
        multiline
        fullWidth
        onChange={this.handleChange("question")}
        margin="normal"
      />
       <Button variant="contained" color="primary" onClick={this.onSubmit}>
        Submit to Whatsapp
      </Button>
        </div>      		);
	}
}
