import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';

const style=theme => ({
  card: {
    display: 'flex',
    float:'left',
    marginTop:'30px',
    width:'100%',
    backgroundColor:'#ff944d'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width:'100%',
   
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    height:100,
    float:'right',
    borderRadius:'100%',
    marginRight:'30px'
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: '10px',
    paddingBottom: '10px',
  },
  playIcon: {
    height: 38,
    width: 38,
  },
});

function MediaControlCard({classes, name}) {

  return (
    <Card className={classes.card}>
      <div className={classes.details}>
        <CardContent className={classes.content}>
         
        
          <Typography >
       <span className='white'><span className='f4 white'>AKASHAYA JYOTISH SANSTHAN</span> <br/> Near kali mandir<br/> Golghar, Gorakhpur-273001 <br/> U.P., India  </span>
          </Typography>
          <Typography >
        <span className=' white'>Email: akashaya08@gmail.com  </span> 
          </Typography>
          <Typography >
       <span className='white'> Whatsapp: 9795751332 </span>
          </Typography>
        </CardContent>
        <div className={classes.controls}>
         
        </div>
      </div>
    
    </Card>
  );
}

export default withStyles(style)(MediaControlCard);