import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';

const style=theme => ({
  card: {
    display: 'flex',
    backgroundColor:'#ff944d'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width:'100%',
    paddingLeft:'20px',
    paddingRight:'20px'
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    height:100,
    float:'right',
    borderRadius:'100%',
    marginRight:'30px'
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: '10px',
    paddingBottom: '10px',
  },
  playIcon: {
    height: 38,
    width: 38,
  },
});

function MediaControlCard({classes, name}) {

  return (
    <Card className={classes.card}>
      <div className={classes.details}>
        <CardContent className={classes.content}>
         
          <Typography >
        <h2 className='f4 fw4 white '>  Ravi Shanker Pandey</h2> 
          </Typography>
          <Typography >
       <span className='i white'>    Astrologer</span>
          </Typography>
        </CardContent>
        <div className={classes.controls}>
         
        </div>
      </div>
      <img
        className={classes.cover}
        src={require(`../../../img/headPhoto.jpeg`)}
       alt={name}
      />
    </Card>
  );
}

export default withStyles(style)(MediaControlCard);