import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Data from '../../../json/panchang.json'
import Button from '@material-ui/core/Button';

var date=new Date().toString();
var exactDate = date.substring(4, 15);

export default class Input extends Component {
    
    handleChange=name=>e=>{
        this.setState({[name]:e.target.value})
    }
    onSubmit=()=>{
        Data.panchang.map((item)=>{
          var zodiac= {}
          Data.panchang.map((item)=>{
              zodiac[`${item.name}`]=this.state[item.name]
          })
          var data={}
          this.props.db.collection('panchang').doc('1').get()
          .then(function(doc) {
           if (doc.exists) {
               data = doc.data()
           } else {
               // doc.data() will be undefined in this case
               console.log("No such document!");
           } }).then(d=>{
               console.log(zodiac)
            data[`${exactDate}`] = zodiac
             let setDoc = this.props.db.collection('panchang').doc('1').set(data)
                    console.log(setDoc)
          
       }).catch(function(error) {
           console.log("Error getting document:", error);
       });
      
         })
         
        
       
       
    }
    render() {	
		return (
        <div className='ma3'>
            <h2 >Panchang Input</h2>
        {Data.panchang.map ((item)=>{  
       return  <TextField
        id="standard-textarea"
        label={`${item.name}`}
        multiline
        fullWidth
        onChange={this.handleChange(item.name)}
        margin="normal"
      />})}
  <Button variant="contained" color="primary" onClick={this.onSubmit}>
        Submit
      </Button>
        </div>      		);
	}
}
