import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Card from '@material-ui/core/Card';
import Headers from './Header'
import Zoom from './Zoom'
import NameCard from './nameCard'
import HoroCard from './HoroCard'

export default class card extends Component {
    constructor(props){
        super(props)
        this.state={
            data:{},
            currentData:{},
            index:0,
            size:0,
        }
    }
    getData=()=>{
        var data={}
        this.props.db.collection('horoscope').doc(this.props.match.params.id).get()
        .then(function(doc) {
            if (doc.exists) {
              data=doc.data()
               console.log(doc.data())
            } else {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
        }).then(d=>{
            this.setState({data:data})
            this.setState({index:Object.keys(this.state.data).length-1})
            this.setState({size:Object.keys(this.state.data).length})
            this.setState({currentData:data[Object.keys(data)[this.state.index]]})
        })
    }
    updateData=(ind)=>{
        this.setState({currentData:this.state.data[Object.keys(this.state.data)[ind]]})
    }
    componentDidMount(){
        if(this.props.db){
            this.getData()
        }
    }
    updateIndex=(dir)=>{
        if(dir==='+'){
            var ind = this.state.index
            this.setState({index:this.state.index+1})
            this.updateData(ind+1)
        }else{
            var ind = this.state.index
            this.setState({index:this.state.index-1})
            this.updateData(ind-1)
        }

    }
	render() {
		console.log(this.state.currentData)
		return (
        <div>
            <Headers data={this.state.currentData} updateIndex={this.updateIndex} size={this.state.size} index={this.state.index}/>
            <div style={{height:'100%',width:'100%'}}>
            <Zoom checked={true}>
                <div className='center ma2'>
                    <div className='center w-70 m-auto' style={{left:'50%'}}>
                        <NameCard name={this.props.match.params.id}/>
                       
         </div> <HoroCard data={this.state.currentData} /></div></Zoom>
            </div>
        </div>      		);
	}
}
