import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Data from '../../../json/starSigns.json'
import Button from '@material-ui/core/Button';

var date=new Date().toString();
var exactDate = date.substring(4, 15) 

export default class Input extends Component {
    
    handleChange=name=>e=>{
        this.setState({[name]:e.target.value})
    }
    onSubmit=()=>{
        Data.starSigns.map((item)=>{
          var zodiac= this.state[item.name]
          var data={}
          this.props.db.collection('horoscope').doc(item.name).get()
          .then(function(doc) {
           if (doc.exists) {
               data = doc.data()
           } else {
               // doc.data() will be undefined in this case
               console.log("No such document!");
           } }).then(d=>{
            data[`${exactDate}`] = {
              name: item.name,
              date: exactDate,
              data:zodiac
            };
             let setDoc = this.props.db.collection('horoscope').doc(item.name).set(data)
                    console.log(setDoc)
          
       }).catch(function(error) {
           console.log("Error getting document:", error);
       });
      
         })
         
        
       
       
    }
    render() {	
		return (
        <div className='ma3'>
        {Data.starSigns.map ((item)=>{  
       return  <TextField
        id="standard-textarea"
        label={`${item.hindi}`}
        multiline
        fullWidth
        onChange={this.handleChange(item.name)}
        margin="normal"
      />})}
  <Button variant="contained" color="primary" onClick={this.onSubmit}>
        Submit
      </Button>
        </div>      		);
	}
}
