import React, { Component } from 'react';

export default class card extends Component {
	render() {
		return (
<div className='flex w-100 mt2 mb2 pa3 white br bt b--light-gray' style={{ backgroundColor:'#ff944d'}}>
				<div className='w-50 f4 fl'>{this.props.name}</div> <div className='w-50 f5 fr'>{this.props.data}</div>
				</div>
		);
	}
}
