import React from 'react'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { styled } from '@material-ui/styles';
import Right from '@material-ui/icons/KeyboardArrowRight';
import Left from '@material-ui/icons/KeyboardArrowLeft';
import IconButton from '@material-ui/core/IconButton';

var date=new Date().toString();
var exactDate = date.substring(4, 15) 

const Navigation = styled(AppBar)({
   height:'50px',
    top:'0',
    backgroundColor:'white'
  });
export default class Header extends React.Component{
    render(){
      const {data,index, size ,updateIndex}=this.props;
      console.log(index, size)
        return(
            <div style={{top:0}}>
            <Navigation position="static">
                <Toolbar>
                <div className='black center flex'>
                <div className='fl ' >  <IconButton onClick={()=>updateIndex('-')} disabled={this.props.index===0} > <Left/></IconButton></div>
            <Typography>
             {data.date   ?<div>{data.date===exactDate? <span className='black f6 center pl6 pr6'>Today</span>
             :<div className='black f6 center pl5 pt1 pr6'>{data.date}</div>}</div>:<span className='black f6 center pl6 pr6'>Loading...</span>}
            </Typography>
            <div className='fr' > <IconButton onClick={()=>updateIndex('+')} disabled={this.props.index===this.props.size-1}><Right/></IconButton></div>
            </div>
                </Toolbar>
            </Navigation>
            </div>        )
    }
} 
