import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { withStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import GridView from '../GridView'
import { styled } from '@material-ui/styles';
import About from '../About/about'
import Query from '../Query/Query'
import Panchang from '../Panchang/Panchang'
const styles =theme => ({
    root: {
      backgroundColor: '#ff6600',
      width: '100%',
    },
  })
  const STab= styled(Tab)({
    fontSize:'13px'
 });
function TabContainer({ children, dir }) {
  return (
    <Typography component="div" dir={dir} >
      {children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
};



function FullWidthTabs({classes, db}) {
  const [value, setValue] = React.useState(0);

  function handleChange(event, newValue) {
    setValue(newValue);
  }

  function handleChangeIndex(index) {
    setValue(index);
  }

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
        >
          <STab label="About Us" />
          <STab label="Horoscope" />
          <STab label="Panchang" />
          <STab label="Queries" />

        </Tabs>
      </AppBar>
      <SwipeableViews
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabContainer><About/></TabContainer>
        <TabContainer><GridView/></TabContainer>
        <TabContainer><Panchang db={db}/></TabContainer>
        <TabContainer><Query/></TabContainer>
      </SwipeableViews>
    </div>
  );
}

export default withStyles(styles)(FullWidthTabs);